<template>
  <div>
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>患者管理</el-breadcrumb-item>
    </el-breadcrumb>
    <div style="margin-bottom: 20px;margin-top: 20px">
      <el-input v-model="params.name" style="width: 200px" placeholder="请输入姓名"></el-input>
      <el-input v-model="params.tel" style="width: 200px; margin-left: 5px" placeholder="请输入电话"></el-input>
      <el-button type="success" style="margin-left: 10px" @click="findBr()">查询</el-button>
      <el-button type="danger" style="margin-left: 10px" @click="clearSearch()">清空</el-button>
      <el-button type="primary" style="margin-left: 380px" @click="addBr()">添加</el-button>
    </div>
    <div>
      <el-table
          v-loading="loading"
          :data="tableData"
          stripe
          border
          size="mini"
          style="width: 70%;">
        <el-table-column type="expand">
          <template slot-scope="props">
            <el-form label-position="left" class="demo-table-expand">
              <el-form-item label="姓名">
                <span>{{ props.row.name }}</span>
              </el-form-item>
              <el-form-item label="性别">
                <span>{{ props.row.sex }}</span>
              </el-form-item>
              <el-form-item label="年龄">
                <span>{{ props.row.age }}</span>
              </el-form-item>
              <el-form-item label="电话">
                <span>{{ props.row.tel }}</span>
              </el-form-item>
              <el-form-item label="来源">
                <span>{{ props.row.bfrom }}</span>
              </el-form-item>
              <el-form-item label="地址">
                <span>{{ props.row.address }}</span>
              </el-form-item>
            </el-form>
          </template>
        </el-table-column>
        <el-table-column
            align="center"
            label-align="center"
            :show-overflow-tooltip="true"
            prop="name"
            width=100
            label="姓名">
        </el-table-column>
        <el-table-column
            align="center"
            label-align="center"
            :show-overflow-tooltip="true"
            prop="sex"
            width=100
            label="性别">
        </el-table-column>
        <el-table-column
            align="center"
            label-align="center"
            :show-overflow-tooltip="true"
            prop="age"
            width=100
            label="年龄">
        </el-table-column>
        <el-table-column
            align="center"
            label-align="center"
            :show-overflow-tooltip="true"
            prop="tel"
            width=120
            label="电话">
        </el-table-column>
        <el-table-column
            align="center"
            label-align="center"
            :show-overflow-tooltip="true"
            prop="bfrom"
            width=100
            label="来源">
        </el-table-column>
        <el-table-column
            align="center"
            label-align="center"
            :show-overflow-tooltip="true"
            prop="address"
            label="地址">
        </el-table-column>
        <el-table-column
            align="center"
            label-align="center"
            fixed="right"
            width=260
            label="操作">
          <template slot-scope="scope">
            <el-button type="primary" @click="editBr(scope.row)">编辑</el-button>
            <RouterLink :to="{name:'bl',params:{bid:scope.row.id}}" style="margin-left: 10px">
              <el-button type="success">病历</el-button>
            </RouterLink>
            <el-popconfirm title="确定删除吗？" @confirm="delBr(scope.row.id)">
              <el-button slot="reference" type="danger" style="margin-left: 10px">删除</el-button>
            </el-popconfirm>
          </template>
        </el-table-column>
      </el-table>
    </div>
    <div>
      <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page="params.pageNum"
          :page-sizes="[5, 10, 50, 100]"
          :page-size="params.pageSize"
          layout="total, sizes, prev, pager, next"
          :total="total"
          style="margin-top: 20px"
      >
      </el-pagination>
    </div>
    <div>
      <el-dialog title="请填写信息" :visible.sync="dialogFormVisible" width="30%" :before-close="CancelDialog"
                 @open="more++">
        <el-form :key="more" :model="form" :rules="rules" ref="form" label-width="100px">
          <el-form-item label="姓名" label-width="15%" prop="name">
            <el-input v-model="form.name" autocomplete="off" style="width: 70%"></el-input>
          </el-form-item>
          <el-form-item label="性别" label-width="15%" prop="sex">
            <el-radio-group v-model="form.sex">
              <el-radio label="男">男</el-radio>
              <el-radio label="女">女</el-radio>
            </el-radio-group>
          </el-form-item>
          <el-form-item label="年龄" label-width="15%" prop="age">
            <el-input v-model="form.age" autocomplete="off" style="width: 70%" type="number"></el-input>
          </el-form-item>
          <el-form-item label="电话" label-width="15%" prop="tel">
            <el-input v-model="form.tel" autocomplete="off" style="width: 70%" type="tel"></el-input>
          </el-form-item>
          <el-form-item label="来源" label-width="15%" prop="bfrom">
            <el-select v-model="form.bfrom" placeholder="请选择病人来源">
              <el-option label="自行就诊" value="自行就诊"></el-option>
              <el-option label="他人介绍" value="他人介绍"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="地址" label-width="15%" prop="address">
            <el-input v-model="form.address" autocomplete="off" style="width: 70%"></el-input>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click="CancelDialog">取 消</el-button>
          <el-button type="success" @click="resetRef">重置</el-button>
          <el-button type="primary" @click="submitBr()">确 定</el-button>
        </div>
      </el-dialog>
    </div>
  </div>
</template>

<script>
import request from "@/utils/request";

export default {
  name: "BrView",
  data() {
    return {
      params: {
        name: '',
        tel: '',
        pageNum: 1,
        pageSize: 5
      },
      total: 0,
      tableData: [],
      dialogFormVisible: false,
      form: {},
      rules: {
        name: [
          {required: true, message: '请输入姓名', trigger: blur}
        ],
        sex: [
          {required: true, message: '请选择性别', trigger: blur}
        ],
        age: [
          {required: true, message: '请输入年龄', trigger: blur}
        ],
        tel: [
          {required: true, message: '请输入电话', trigger: blur}
        ],
        address: [],
        bfrom: [],
      },
      more: 0,
      loading: true
    }
  },
  created() {
    this.findBr()
  },
  methods: {
    load() {
      request.get("/findAllBr").then(res => {
        if (res.state === 0) {
          this.tableData = res.data;
        }
      })
    },

    findBr() {
      request.get("/findBr", {
        params: this.params
      }).then(res => {
        if (res.state === 0) {
          this.tableData = res.data.list;
          this.total = res.data.total;
          this.loading = false;
          this.$message({
            message: '查询成功',
            type: 'success'
          });
        } else {
          this.$message.error(res.message);
          this.$router.push("/login")
        }
      })
    },

    clearSearch() {
      this.params = {
        pageNum: 1,
        pageSize: 5,
        name: '',
        phone: ''
      }
      this.findBr()
    },

    handleSizeChange(pageSize) {
      this.params.pageSize = pageSize;
      this.findBr();
    },

    handleCurrentChange(pageNum) {
      this.params.pageNum = pageNum;
      this.findBr();
    },

    addBr() {
      this.form = {};
      this.dialogFormVisible = true;
    },
    editBr(obj) {
      this.form = obj;
      this.dialogFormVisible = true;
    },
    submitBr() {
      this.$refs.form.validate((valid) => {
        if (valid) {
          request.post("/saveBr", this.form).then(res => {
            if (res.state === 0) {
              this.$message({
                message: '操作成功',
                type: 'success'
              });
              this.dialogFormVisible = false
              this.findBr()
            } else {
              this.$message.error(res.message);
            }
          })
        }
      });
    },

    delBr(id) {
      request.delete("/delBr/" + id).then(res => {
        if (res.state === 0) {
          this.$message({
            message: '删除成功',
            type: 'success'
          });
          this.findBr()
        } else {
          this.$message.error(res.message);
        }
      })
    },

    resetRef() {
      this.$refs.form.resetFields();
    },

    CancelDialog() {
      this.dialogFormVisible = false;
      this.resetRef();
    }
  }
}
</script>

<style scoped>

</style>